.skills {
  padding: 8rem 0;
  background: #f8fafc;
}

.skills-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 3rem;
}

.skills-content {
  margin-top: 3rem;
}

.skill-category-section {
  margin-bottom: 5rem;
}

.skill-category-section:last-child {
  margin-bottom: 0;
}

.category-title {
  font-size: 2.2rem;
  font-weight: 700;
  color: #1f2937;
  margin-bottom: 3rem;
  text-align: center;
  position: relative;
}

.category-title::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background: #3b82f6;
  border-radius: 2px;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  max-width: 800px;
  margin: 0 auto;
}

.skill-item {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem 2rem;
  background: white;
  border-radius: 25px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  border: 2px solid #e2e8f0;
  transition: all 0.3s ease;
}

.skill-item:hover {
  border-color: #3b82f6;
  box-shadow: 0 12px 30px rgba(59, 130, 246, 0.2);
}

.skill-name {
  font-size: 1.1rem;
  font-weight: 600;
  color: #1f2937;
  text-align: center;
  line-height: 1.3;
}

.section-title {
  font-size: 3rem;
  font-weight: 700;
  color: #1f2937;
  text-align: center;
  margin-bottom: 4rem;
  position: relative;
}

.section-title::after {
  content: '';
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 4px;
  background: #3b82f6;
  border-radius: 2px;
}

@media (max-width: 1024px) {
  .skills-container {
    padding: 0 2rem;
  }

  .skills-list {
    gap: 0.8rem;
  }

  .skill-item {
    padding: 0.8rem 1.5rem;
  }
}

@media (max-width: 768px) {
  .skills {
    padding: 6rem 0;
  }

  .skills-container {
    padding: 0 1rem;
  }

  .section-title {
    font-size: 2.5rem;
  }

  .category-title {
    font-size: 1.8rem;
  }

  .skills-list {
    gap: 0.8rem;
  }

  .skill-item {
    padding: 0.8rem 1.2rem;
  }

  .skill-name {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .skills-list {
    gap: 0.6rem;
  }

  .skill-item {
    padding: 0.6rem 1rem;
  }

  .skill-name {
    font-size: 0.9rem;
  }
}
