.hero {
  min-height: 120vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #3b82f6 0%, #1e40af 100%);
  position: relative;
  overflow: hidden;
  padding-top: 80px;
  padding-bottom: 100px;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

.hero-container {
  max-width: 1400px;
  width: 100%;
  margin: 0 auto;
  padding: 0 3rem;
  position: relative;
  z-index: 1;
}

.hero-content {
  display: flex;
  flex-direction: column;
  gap: 4rem;
}

/* 프로필 섹션 */
.profile-section {
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: 4rem;
  align-items: center;
}

.profile-left {
  display: flex;
  justify-content: center;
}

.profile-image {
  width: 250px;
  height: 300px;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
  border: 2px solid rgba(255, 255, 255, 0.3);
}

.image-placeholder {
  text-align: center;
}

.profile-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
  opacity: 0.7;
}

.image-placeholder p {
  font-size: 1rem;
  color: #6b7280;
  font-weight: 500;
  margin: 0;
}

.profile-right {
  color: white;
}

.hero-greeting {
  font-size: 1.4rem;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 400;
  letter-spacing: 0.5px;
  margin-bottom: 1rem;
}

.hero-name {
  font-size: 3rem;
  font-weight: 700;
  color: white;
  margin: 0 0 2rem 0;
  line-height: 1.2;
}

.hero-name .highlight {
  color: #60a5fa;
}

.profile-info {
  margin-bottom: 2rem;
}

.info-item {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.info-label {
  font-weight: 700;
  color: #60a5fa;
  min-width: 80px;
  margin-right: 1rem;
}

.info-value {
  color: rgba(255, 255, 255, 0.9);
}

/* 학력 및 활동 섹션 */
.education-section {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 25px;
  padding: 2.5rem;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
}

.education-section h2 {
  font-size: 1.8rem;
  font-weight: 700;
  color: #1f2937;
  text-align: center;
  margin-bottom: 2rem;
}

.education-grid {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  max-width: 800px;
  margin: 0 auto;
}

.education-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  background: white;
  border-radius: 15px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  border-left: 4px solid #3b82f6;
}

.education-content {
  flex: 1;
}

.education-content h4 {
  font-size: 1.1rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0;
}

.education-content p {
  font-size: 1rem;
  color: #6b7280;
  margin: 0;
}

.education-period {
  font-size: 0.9rem;
  color: #3b82f6;
  font-weight: 600;
  white-space: nowrap;
  margin-left: 2rem;
}

.hero-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
}

.hero-btn {
  padding: 1.2rem 2.5rem;
  border-radius: 50px;
  font-size: 1.1rem;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 160px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.hero-btn.primary {
  background: #3b82f6;
  color: white;
  box-shadow: 0 8px 25px rgba(59, 130, 246, 0.3);
}

.hero-btn.primary:hover {
  background: #2563eb;
  transform: translateY(-3px);
  box-shadow: 0 15px 35px rgba(59, 130, 246, 0.4);
}

.hero-btn.secondary {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px);
}

.hero-btn.secondary:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-3px);
  box-shadow: 0 15px 35px rgba(255, 255, 255, 0.2);
}

@media (max-width: 1024px) {
  .hero-container {
    padding: 0 2rem;
  }

  .profile-section {
    grid-template-columns: 1fr;
    gap: 3rem;
    text-align: center;
  }

  .profile-left {
    order: -1;
  }

  .hero-name {
    font-size: 2.5rem;
  }

  .education-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }

  .education-period {
    margin-left: 0;
  }
}

@media (max-width: 768px) {
  .hero {
    padding-top: 70px;
  }

  .hero-container {
    padding: 0 1rem;
  }

  .hero-name {
    font-size: 2rem;
  }

  .profile-image {
    width: 200px;
    height: 250px;
  }

  .education-section {
    padding: 2rem;
  }

  .education-item {
    padding: 1.5rem;
  }

  .hero-buttons {
    flex-direction: column;
    align-items: center;
  }

  .hero-btn {
    width: 200px;
  }
}
